@charset "UTF-8";
*,
*::before,
*::after {
  box-sizing: border-box;
}

:root {
  font-size: null;
}

@-ms-viewport {
  width: device-width;
}

body {
  margin: 0;
  font-family: "Noto Sans Japanese", sans-serif;
  font-size: 1rem;
  font-weight: normal;
  line-height: 1.5;
  color: #000;
  background-color: #fff;
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0.3);
}

[tabindex="-1"]:focus:not(:focus-visible) {
  outline: 0 !important;
}

hr {
  margin: 0;
  color: inherit;
  background-color: currentColor;
  border: 0;
  opacity: .25;
}

hr:not([size]) {
  height: 1px;
}

h1, h2, h3, h4, h5, h6 {
  margin: 1rem 0 0;
}

p {
  margin: 1rem 0 0;
}

abbr[title],
abbr[data-original-title] {
  text-decoration: underline;
  text-decoration: underline dotted;
  cursor: help;
  text-decoration-skip-ink: none;
}

address {
  margin: 1rem 0 0;
  font-style: normal;
  line-height: inherit;
}

ol,
ul,
dl {
  margin: 1rem 0 0;
}

ol ol,
ul ul,
ol ul,
ul ol {
  margin: 0;
}

dt {
  font-weight: normal;
}

dd {
  margin: 1rem 0 0;
}

blockquote {
  margin: 1rem 0 0;
}

b,
strong {
  font-weight: bolder;
}

small {
  font-size: 80%;
}

sub,
sup {
  position: relative;
  font-size: .75em;
  line-height: 0;
  vertical-align: baseline;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

a {
  color: #0275d8;
  text-decoration: none;
}

a:hover, a:focus {
  color: #5bc0de;
  text-decoration: underline;
}

a:not([href]), a:not([href]):hover {
  color: inherit;
  text-decoration: none;
}

pre,
code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

pre {
  display: block;
  margin: 1rem 0 0;
  overflow: auto;
  font-size: .875em;
}

pre code {
  font-size: inherit;
  color: inherit;
  word-break: normal;
}

code {
  font-size: .875em;
  color: #d63384;
  word-wrap: break-word;
}

a > code {
  color: inherit;
}

kbd {
  padding: .2rem .4rem;
  font-size: .875em;
  color: #fff;
  border-radius: .2rem;
}

kbd kbd {
  padding: 0;
  font-size: 1em;
  font-weight: 700;
}

figure {
  margin: 1rem 0 0;
}

img {
  vertical-align: middle;
}

svg {
  overflow: hidden;
  vertical-align: middle;
}

table {
  border-collapse: collapse;
}

caption {
  text-align: left;
  caption-side: bottom;
}

th {
  text-align: inherit;
}

label {
  display: inline-block;
}

button {
  border-radius: 0;
}

button:focus {
  outline: 1px dotted;
  outline: 5px auto -webkit-focus-ring-color;
}

input,
button,
select,
optgroup,
textarea {
  margin: 0;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

select {
  word-wrap: normal;
}

[list]::-webkit-calendar-picker-indicator {
  display: none;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}

button:not(:disabled),
[type="button"]:not(:disabled),
[type="reset"]:not(:disabled),
[type="submit"]:not(:disabled) {
  cursor: pointer;
}

button::-moz-focus-inner,
::-moz-focus-inner {
  padding: 0;
  border-style: none;
}

input[type="date"],
input[type="time"],
input[type="datetime-local"],
input[type="month"] {
  -webkit-appearance: listbox;
}

textarea {
  overflow: auto;
  resize: vertical;
}

fieldset {
  min-width: 0;
  padding: 0;
  margin: 0;
  border: 0;
}

legend {
  float: left;
  width: 100%;
  padding: 0;
  margin: 1rem 0 0;
  font-size: 1.5rem;
  line-height: inherit;
  color: inherit;
  white-space: normal;
}

mark {
  padding: .2em;
  background-color: #fcf8e3;
}

progress {
  vertical-align: baseline;
}

::-webkit-datetime-edit {
  overflow: visible;
  line-height: 0;
}

[type="search"] {
  outline-offset: -2px;
  -webkit-appearance: textfield;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-color-swatch-wrapper {
  padding: 0;
}

::-webkit-file-upload-button {
  font: inherit;
  -webkit-appearance: button;
}

output {
  display: inline-block;
}

summary {
  display: list-item;
  cursor: pointer;
}

template {
  display: none;
}

main {
  display: block;
}

[hidden] {
  display: none !important;
}

/* ==================================================
   フォーム
 ================================================== */
.form-control {
  display: block;
  width: 100%;
  padding: .5rem .75rem;
  font-size: 1rem;
  line-height: 1.25;
  color: #495057;
  background-color: #fff;
  background-image: none;
  background-clip: padding-box;
  border: 1px solid rgba(0, 0, 0, 0.15);
  border-radius: 0;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
  transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
}

.form-control::-ms-expand {
  background-color: transparent;
  border: 0;
}

.form-control:focus {
  color: #495057;
  background-color: #fff;
  border-color: #80bdff;
  outline: none;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
}

.form-control::placeholder {
  color: #868e96;
  opacity: 1;
}

.form-control:disabled, .form-control[readonly] {
  background-color: #e9ecef;
  opacity: 1;
}

select.form-control:not([size]):not([multiple]) {
  height: calc(2.25rem + 2px);
}

select.form-control:focus::-ms-value {
  color: #495057;
  background-color: #fff;
}

.form-control-file,
.form-control-range {
  display: block;
}

.col-form-legend {
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  margin-bottom: 0;
  font-size: 1rem;
}

.form-control-plaintext {
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  margin-bottom: 0;
  line-height: 1.25;
  background-color: transparent;
  border: solid transparent;
  border-width: 1px 0;
}

.form-group {
  margin-bottom: 1rem;
}

.form-text {
  display: block;
  margin-top: .25rem;
}

.form-check {
  position: relative;
  display: block;
  margin-bottom: .5rem;
}

.form-check.disabled .form-check-label {
  color: #868e96;
}

.form-check-label {
  padding-left: 1.25rem;
  margin-bottom: 0;
}

.form-check-input {
  position: absolute;
  margin-top: .25rem;
  margin-left: -1.25rem;
}

.form-check-input:only-child {
  position: static;
}

.form-check-inline {
  display: inline-block;
  margin-right: .25rems;
}

.form-check-inline + .form-check-inline {
  margin-left: .75rem;
}

.form-check-inline .form-check-label {
  vertical-align: middle;
}

.form-inline {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
}

.form-inline .form-check {
  width: 100%;
}

.form-inline label {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 0;
}

.form-inline .form-group {
  display: flex;
  flex: 0 0 auto;
  flex-flow: row wrap;
  align-items: center;
  margin-bottom: 0;
}

.form-inline .form-control {
  display: inline-block;
  width: auto;
  vertical-align: middle;
}

.form-inline .form-control-plaintext {
  display: inline-block;
}

.form-inline .input-group {
  width: auto;
}

.form-inline .form-control-label {
  margin-bottom: 0;
  vertical-align: middle;
}

.form-inline .form-check {
  display: flex;
  align-items: center;
  justify-content: center;
  width: auto;
  margin-top: 0;
  margin-bottom: 0;
}

.form-inline .form-check-label {
  padding-left: 0;
}

.form-inline .form-check-input {
  position: relative;
  margin-top: 0;
  margin-right: .25rem;
  margin-left: 0;
}

.input-group-addon:not(:first-child):not(:last-child),
.input-group-btn:not(:first-child):not(:last-child),
.input-group .form-control:not(:first-child):not(:last-child) {
  border-radius: 0;
}

.input-group-addon,
.input-group-btn {
  vertical-align: middle;
  white-space: nowrap;
}

.input-group-addon {
  padding: 0.5rem 0.75rem;
  margin-bottom: 0;
  font-size: 1rem;
  font-weight: normal;
  line-height: 1.25;
  color: #495057;
  text-align: center;
  background-color: #e9ecef;
  background-clip: padding-box;
  border: 1px solid rgba(0, 0, 0, 0.15);
  border-radius: 0.25rem;
}

.input-group-addon input[type="radio"],
.input-group-addon input[type="checkbox"] {
  margin-top: 0;
}

.input-group .form-control:not(:last-child),
.input-group-addon:not(:last-child),
.input-group-btn:not(:last-child) > .btn,
.input-group-btn:not(:last-child) > .btn-group > .btn,
.input-group-btn:not(:last-child) > .dropdown-toggle,
.input-group-btn:not(:first-child) > .btn:not(:last-child):not(.dropdown-toggle),
.input-group-btn:not(:first-child) > .btn-group:not(:last-child) > .btn {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}

.input-group-addon:not(:last-child) {
  border-right: 0;
}

.input-group .form-control:not(:first-child),
.input-group-addon:not(:first-child),
.input-group-btn:not(:first-child) > .btn,
.input-group-btn:not(:first-child) > .btn-group > .btn,
.input-group-btn:not(:first-child) > .dropdown-toggle,
.input-group-btn:not(:last-child) > .btn:not(:first-child),
.input-group-btn:not(:last-child) > .btn-group:not(:first-child) > .btn {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}

.form-control + .input-group-addon:not(:first-child) {
  border-left: 0;
}

/* ==================================================
   ボタン
 ================================================== */
.btn {
  display: inline-block;
  font-weight: 400;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  user-select: none;
  border: 1px solid transparent;
  padding: 0.5rem 0.75rem;
  font-size: 1rem;
  line-height: 1.25;
  border-radius: .25rem;
  cursor: pointer;
  transition: all .15s ease-in-out;
}

.btn:focus, .btn:hover {
  text-decoration: none;
}

.btn:focus, .btn.focus {
  outline: 0;
  box-shadow: 0 0 0 3px rgba(0, 123, 255, 0.25);
}

.btn.disabled, .btn:disabled {
  opacity: .65;
  box-shadow: none;
}

.btn:active, .btn.active {
  background-image: none;
  box-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
}

/* ==================================================
   モーダル
 ================================================== */
.modal-open {
  overflow: hidden;
}

.modal-open .modal {
  overflow-x: hidden;
  overflow-y: auto;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1050;
  display: none;
  width: 100%;
  height: 100%;
  overflow: hidden;
  outline: 0;
}

.modal-dialog {
  position: relative;
  width: auto;
  margin: 10px;
  pointer-events: none;
}

.modal.fade .modal-dialog {
  transition: transform .3s ease-out;
  transform: translate(0, -25%);
}

.modal.show .modal-dialog {
  transform: translate(0, 0);
}

@media (min-width: 768px) {
  .modal-dialog {
    max-width: 600px;
    margin: 1.75rem auto;
  }
}

.modal-content {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  pointer-events: auto;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: .3rem;
  box-shadow: 0 3px 9px rgba(0, 0, 0, 0.5);
  outline: 0;
}

@media (min-width: 768px) {
  .modal-content {
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.5);
  }
}

.modal-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1040;
  width: 100vw;
  height: 100vh;
  background-color: #000;
}

.modal-backdrop.fade {
  opacity: 0;
}

.modal-backdrop.show {
  opacity: .5;
}

.modal-header {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding: 1rem;
  border-bottom: 1px solid #dee2e6;
}

.modal-header .close {
  padding: 1rem;
  margin: -1rem -1rem -1rem auto;
}

.modal-title {
  margin-bottom: 0;
}

.modal-body {
  position: relative;
  flex: 1 1 auto;
  padding: 1rem;
}

.modal-footer {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-end;
  padding: 1rem;
  border-top: 1px solid #dee2e6;
}

.modal-footer > * {
  margin: calc(.5rem / 2);
}

.modal-scrollbar-measure {
  position: absolute;
  top: -9999px;
  width: 50px;
  height: 50px;
  overflow: scroll;
}

#modalOverlay {
  width: 100%;
  height: 100%;
  z-index: 1020;
  /* places the modal overlay between the main page and the modal dialog*/
  background-color: #000;
  opacity: 0.5;
  position: fixed;
  top: 0;
  left: 0;
  display: none;
  margin: 0;
  padding: 0;
}

#modalOverlay:hover {
  cursor: pointer;
}

#acc-modal {
  margin-left: auto;
  margin-right: auto;
  padding: 5px;
  border: thin #000 solid;
  background-color: #fff;
  z-index: 1030;
  /* places the modal dialog on top of everything else */
  position: fixed;
  top: 5%;
  width: 94%;
  display: none;
}

@media (min-width: 768px) {
  #acc-modal {
    top: 10%;
    left: 10%;
    width: 80%;
  }
}

@media (min-width: 980px) {
  #acc-modal {
    top: 25%;
    left: 25%;
    width: 50%;
  }
}

#modal h1 {
  text-align: center;
}

.modalCloseButton {
  position: absolute;
  top: 10px;
  right: 10px;
  height: 25px;
}

.modalCloseButton img {
  border: 0;
}

.screen-reader-offscreen {
  position: absolute;
  left: -999px;
  width: 1px;
  height: 1px;
  top: auto;
}

/* ==================================================
   page-example.html
 ================================================== */
.wrap {
  width: 100%;
  padding: 0 14px;
}

@media (min-width: 980px) {
  .wrap {
    width: 980px;
    margin: 0 auto;
  }
}

/* --------------------------------------------------
   メニュー全体
 -------------------------------------------------- */
.menu {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  height: 75px;
  background-color: #efefef;
  z-index: 10;
}

@media (min-width: 768px) {
  .menu {
    height: auto;
    background-color: #fff;
  }
}

/* --------------------------------------------------
   メニューボタン
 -------------------------------------------------- */
.menu-btn {
  display: inline-block;
  position: absolute;
  top: 10px;
  right: 10px;
  width: 52px;
  height: 52px;
  padding: 0;
  background-color: #fff;
  border: none;
}

.menu-btn:hover {
  cursor: pointer;
}

@media (min-width: 768px) {
  .menu-btn {
    display: none;
  }
}

.menu-btn_line {
  display: inline-block;
  position: absolute;
  left: 5px;
  width: 40px;
  height: 2px;
  background-color: #0275d8;
}

.menu-btn_line:nth-child(1) {
  top: 16px;
}

.menu-btn_line:nth-child(2) {
  top: 25px;
}

.menu-btn_line:nth-child(3) {
  top: 34px;
}

/* --------------------------------------------------
  メインナビゲーション
-------------------------------------------------- */
.gnav[aria-hidden] {
  transition: visibility .2s;
}

.gnav[aria-hidden] .gnav-backdrop {
  transition: opacity .2s;
}

.gnav[aria-hidden] ul {
  transition: transform .2s;
}

.gnav[aria-hidden="false"] {
  visibility: visible;
}

.gnav[aria-hidden="false"] .gnav-backdrop {
  opacity: 1;
}

.gnav[aria-hidden="false"] ul {
  transform: translate(calc(100% - 72%), 0);
}

@media (min-width: 768px) {
  .gnav[aria-hidden="false"] ul {
    transform: translate(0, 0);
  }
}

.gnav[aria-hidden="true"] {
  visibility: hidden;
  height: 1px;
}

@media (min-width: 768px) {
  .gnav[aria-hidden="true"] {
    visibility: visible;
    height: auto;
  }
}

.gnav[aria-hidden="true"] .gnav-backdrop {
  opacity: 0;
}

.gnav[aria-hidden="true"] ul {
  transform: translate(calc(100% + 28%), 0);
}

@media (min-width: 768px) {
  .gnav[aria-hidden="true"] ul {
    transform: translate(0, 0);
  }
}

.gnav ul {
  padding-left: 0;
  list-style: none;
  margin: 0;
  position: relative;
  top: 76px;
  width: 78%;
  max-height: calc(100vh - 140px);
  background-color: #3d3d3d;
  color: #fff;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}

@media (min-width: 768px) {
  .gnav ul {
    display: flex;
    position: static;
    width: auto;
    max-height: none;
    background-color: transparent;
    color: #000;
    overflow: visible;
  }
}

.gnav li {
  border-bottom: 1px solid #fff;
}

@media (min-width: 768px) {
  .gnav li {
    flex: 1 0 20%;
    text-align: center;
    border-bottom: none;
  }
}

.gnav a {
  display: block;
  padding: 14px 10px;
  background-color: #0275d8;
  color: #fff;
}

@media (min-width: 768px) {
  .gnav a {
    padding: 10px 0;
    background-color: transparent;
    color: #0275d8;
  }
}

.gnav a:hover, .gnav a:focus {
  background-color: #5bc0de;
  text-decoration: none;
}

@media (min-width: 768px) {
  .gnav a:hover, .gnav a:focus {
    background-color: transparent;
    text-decoration: underline;
  }
}

/* --------------------------------------------------
   バックドロップ
 -------------------------------------------------- */
.gnav-backdrop {
  position: fixed;
  top: 76px;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.75);
}

@media (min-width: 768px) {
  .gnav-backdrop {
    display: none;
  }
}

/* --------------------------------------------------
   メイン
 -------------------------------------------------- */
.page-example .main {
  margin: 70px 0 0 0;
}

/* --------------------------------------------------
   タブパネル
 -------------------------------------------------- */
.tab-content > .tab-content_pane {
  display: none;
}

.tab-content > .active {
  display: block;
}

/* --------------------------------------------------
   リンク下線
 -------------------------------------------------- */
.underlink {
  position: relative;
}

.underlink:after {
  content: '';
  display: inline-block;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 1px;
  background-color: #0275d8;
  transition: width .3s ease-in;
}

.underlink:hover {
  text-decoration: none;
}

.underlink:hover:after {
  width: 100%;
}

/* --------------------------------------------------
   カラー currentColor
 -------------------------------------------------- */
.link-current {
  color: #6f2dbd;
}

.link-current a {
  color: currentColor;
  border: 4px solid;
  transition: color .5s;
}

.link-current a:hover {
  color: #0275d8;
}

/* --------------------------------------------------
   Floating label
 -------------------------------------------------- */
.floating-label {
  position: relative;
}

.floating-label input {
  padding: 6px;
  border: 1px solid;
  font-size: 1.125rem;
}

.floating-label input:focus {
  padding: 10px 6px 2px 6px;
  box-shadow: 0 0 0 1px #000;
  outline: none;
}

.floating-label input:required:valid {
  padding: 10px 6px 2px 6px;
}

.floating-label input:focus + label,
.floating-label input:required:valid + label {
  transform: translateY(-20px);
  font-size: 0.625rem;
}

.floating-label label {
  position: absolute;
  top: 50%;
  left: 10px;
  color: #555;
  transform: translateY(-50%);
  transition: font-size .2s ease-out, transform .2s ease-out;
}

.filter {
  background-color: #fff;
  filter: blur(5px) contrast(10);
}

.filter_inner01,
.filter_inner02 {
  width: 100px;
  height: 100px;
  background-color: #000;
  border-radius: 50%;
}

/* --------------------------------------------------
   コインアルバム
 -------------------------------------------------- */
.coin-album {
  padding-left: 0;
  list-style: none;
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: repeat(4, 1fr);
  margin: 0;
  padding: 0;
  gap: 24px;
}

.coin-album img {
  max-width: 100%;
  height: auto;
}

.coin-album02 {
  padding-left: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(100px, auto));
  gap: 10px;
}

.coin-album02 img {
  max-width: 100%;
