/* --------------------------------------------------
   ボタン
 -------------------------------------------------- */
.btn-effect {
  display: inline-block;
  position: relative;
  width: 230px;
  height: 100px;
  padding: 0;
  background-color: #a17923;
  border: none;
  border-radius: 4px;
  overflow: hidden;
}

.btn-effect:hover .btn-effect_effect, .btn-effect:active .btn-effect_effect {
  transform: translateX(0);
}

.btn-effect_effect {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100px;
  background-color: rgba(0, 0, 0, 0.25);
  transform: translateX(-100%);
  transition: transform .3s ease-out;
}

.btn-effect_txt {
  color: #fff;
  font-size: 2rem;
}

/* --------------------------------------------------
   コインアルバム getJSON
 -------------------------------------------------- */
.is-album {
  padding-left: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, auto));
  gap: 20px;
}

.is-album img {
  max-width: 100%;
  height: auto;
}

/* --------------------------------------------------
   背景イメージホバー拡大
 -------------------------------------------------- */
.bg-img-hover {
  display: block;
  width: 300px;
}

.bg-img-hover:hover .bg-img-hover_img img {
  transform: scale(1.1);
}

.bg-img-hover_img {
  margin: 0;
  width: 300px;
  height: 218px;
  overflow: hidden;
}

.bg-img-hover_img img {
  transition: transform .3s;
}

.bg-img-hover_txt {
  margin: 4px 0 0 0;
}

/* --------------------------------------------------
