  bottom: 40px;
  right: 40px;
}

/* --------------------------------------------------
   角丸マウスホバー
 -------------------------------------------------- */
.border {
  position: relative;
  width: 200px;
  overflow: hidden;
  border-radius: 10px;
  z-index: 2;
}

.border img {
  transition: transform .2s;
}

.border:hover {
  cursor: pointer;
}

.border:hover img {
  transform: scale(1.2);
}

/* --------------------------------------------------
   斜めベタ背景
 -------------------------------------------------- */
.diagonal {
  height: 100px;
  background: linear-gradient(175deg, #A663CC, #A663CC 55%, #6F2DBD calc(55% + 1px), #6F2DBD 100%);
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
}

/* --------------------------------------------------
   2つのリンク 離れた箇所と連携
 -------------------------------------------------- */
.two-link {
  position: relative;
  padding: 0 400px 0 0;
}

.two-link p {
  margin: 0;
}

.two-link_link01 {
  color: #a663cc;
}

.two-link_link01:after {
  content: '';
  display: inline-block;
  position: absolute;
  top: 20px;
  right: 40px;
  width: 100px;
  height: 100px;
  background-color: currentColor;
  opacity: .85;
  transition: transform .5s, opacity .5s;
}

.two-link_link01:hover:after {
  transform: scale(1.2);
  transform: rotate(10deg);
  opacity: 1;
}

.two-link_link02 {
  color: #6f2dbd;
}

.two-link_link02:after {
  content: '';
  display: inline-block;
  position: absolute;
  top: 40px;
  right: 100px;
  width: 100px;
  height: 100px;
  background-color: currentColor;
  opacity: .85;
  transition: transform .5s, opacity .5s;
}

.two-link_link02:hover:after {
  transform: scale(1.2);
  transform: rotate(10deg);
  opacity: 1;
}

