/* --------------------------------------------------
   モーダル target擬似クラス使用
 -------------------------------------------------- */
#modal {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 1050;
}

#modal:not(:target) {
  opacity: 0;
  visibility: hidden;
}

#modal:target {
  opacity: 1;
  visibility: visible;
}

.modal_overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.modal_content {
  position: relative;
  width: 70%;
  background-color: #fff;
  padding: 20px;
}

.modal-scroll-stop {
  overflow: hidden;
  height: 100%;
}

/* --------------------------------------------------
   次項目オープンクローズ
 -------------------------------------------------- */
#next-open:not(:target) {
  display: none;
}

#next-open:target {
  display: block;
}

